.iwanttogo {
  padding-top: 193px;
  padding-bottom: 231px;
  background-image: url("../img/backpack.png");
  background-repeat: no-repeat;
  background-size: 147px 262px;
  background-position: left 117px;
  overflow: hidden;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/backpack@2x.png");
  }
}

.iwanttogo__wrapper {
  margin: 0 auto;
  padding-left: 30px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  max-width: 1005px;
}

.iwanttogo__call {
  width: 477px;
}

.iwanttogo__call h2 {
  margin: 0;
  margin-bottom: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 49px;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  color: $blue;
}

.iwanttogo__call p {
  margin: 0 auto;
  margin-bottom: 63px;
  padding-right: 50px;
  max-width: 720px;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 17px;
  line-height: 29px;
  color: $black;
}

.iwanttogo__form {
  position: relative;
}

.iwanttogo__form input {
  padding-top: 19px;
  padding-bottom: 20px;
  padding-left: 40px;
  width: 100%;
  border-radius: 80px;
  border: 2px solid #e3e3e3;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  text-transform: uppercase;
  color: $black;

  &:focus,
  &:focus:valid {
    border: 2px solid $yellow;
    outline: 0;
  }

  &:invalid:not(:placeholder-shown):not(:focus) {
    border: 2px solid rgba(255, 0, 0, 0.5);
  }

  &:valid {
    border: 2px solid rgba(72, 72, 72, 0.5);
  }
}

.iwanttogo button {
  width: 216px;
  padding-top: 19px;
  padding-bottom: 20px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  border: 2px solid transparent;
  border-radius: 80px;
  background-image: $gradient;
  text-align: center;
  text-transform: uppercase;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
  color: #ffffff;
  z-index: 2;
  cursor: pointer;

  &:hover {
    background: $gradient__hover;
  }

  &:active {
    background: $gradient__active;
  }
}

.iwanttogo img {
  position: absolute;
  right: -18%;
  top: -83%;
  z-index: -1;
  width: auto;
  max-width: 818px;
  max-height: 594px;
}

@media (max-width: $tablet-max) {
  .iwanttogo img {
    transform: scale(0.7);
  }

  .iwanttogo__call {
    width: 50%;
  }

  .iwanttogo__call p {
    padding-right: 170px;
  }
}

@media (max-width: $mobile-max) {
  .iwanttogo {
    padding: 0;
    padding-bottom: 37px;
  }

  .iwanttogo__wrapper {
    padding-left: 16px;
    padding-right: 16px;
    flex-direction: column;
    width: 100%;
  }

  .iwanttogo img {
    position: static;
    width: 100%;
    transform: none;
    min-width: 424px;
  }

  .iwanttogo__call {
    width: 100%;
  }

  .iwanttogo__call h2 {
    font-size: 32px;
    line-height: 39px;
    text-align: center;
  }

  .iwanttogo__call p {
    margin-bottom: 24px;
    padding: 0;
    font-size: 16px;
    line-height: 26px;
  }

  .iwanttogo__form input {
    margin-bottom: 8px;
    padding-top: 18px;
    padding-bottom: 17px;
    text-transform: lowercase;
  }

  .iwanttogo button {
    margin: 0 auto;
    padding-bottom: 16px;
    display: block;
    width: 264px;
    position: static;
    font-size: 15px;
    transform: none;
  }
}
